@import url("https://fonts.googleapis.com/css?family=Nunito:200,300,400&display=swap");
/* Rules for sizing the icon. */
.material-icons.md-18 {
  font-size: 18px;
}

.material-icons.md-24 {
  font-size: 24px;
}

.material-icons.md-36 {
  font-size: 36px;
}

.material-icons.md-48 {
  font-size: 48px;
}

.material-icons.md-96 {
  font-size: 96px;
}

.material-icons.md-1 {
  font-size: 1rem;
}

.material-icons.md-1-5 {
  font-size: 1.5rem;
}

.material-icons.md-2 {
  font-size: 2rem;
}

.material-icons.md-3 {
  font-size: 3rem;
}

/* Rules for using icons as black on a light background. */
.material-icons.md-dark {
  color: rgba(0, 0, 0, 0.54);
}

.material-icons.md-dark.md-inactive {
  color: rgba(0, 0, 0, 0.26);
}

/* Rules for using icons as white on a dark background. */
.material-icons.md-light {
  color: white;
}

.material-icons.md-light.md-inactive {
  color: rgba(255, 255, 255, 0.3);
}

.material-icons {
  font-size: inherit;
  line-height: inherit;
  vertical-align: text-bottom;
}

.material-icons.h1 {
  font-size: 2rem;
}

.material-icons.display-1 {
  font-size: 4.5rem;
}

.md-36-24 {
  position: relative;
  top: 5px;
}

.clickable {
  cursor: pointer;
}

.noselect {
  user-select: none;
}

.hoverchild {
  z-index: 100;
  opacity: 0;
  transition: all 0.5s;
}

.hoverparent:hover .hoverchild {
  opacity: 1;
}

.abosolute-top-right {
  position: absolute;
  top: 1rem;
  right: 1rem;
}

.ratio-16-9 {
  width: 100%;
  padding-bottom: 67%;
  position: relative;
}

.ratio-content {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

@tailwind base;
@tailwind components;
@tailwind utilities;